.Login {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 180px;
    height: 100vh;
    background: linear-gradient(to bottom, #35546f, #1b3c58);
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
  }
  
  .content-wrapper {
    background: #2b4964;
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    width: 100%;
    z-index: 2;
  }
  
  .login-title {
    font-size: 24px;
    color: #ffffff;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .login-description {
    font-size: 16px;
    color: #d0d0d0;
    text-align: center;
    margin-bottom: 30px;
    line-height: 1.6;
    max-width: 700px;
  }
  
  .understand-button {
    background: radial-gradient(#274660, #375872);
    padding: 12px 30px;
    border-radius: 30px;
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: transform 0.2s;
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 2;
  }
  
  .understand-button:hover {
    transform: scale(0.98);
  }
  
  .understand-button:active {
    transform: scale(0.95);
  }
  
  .floating-word {
    position: absolute;
    font-size: 18px;
    color: #6396C3;
    opacity: 0.7;
    animation: float 5s infinite ease-in-out;
    z-index: 1;
  }
  
  @keyframes float {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-20px);
    }
    100% {
      transform: translateY(0);
    }
  }
  