.Downloads {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 180px;
  height: 100vh;
  background: linear-gradient(to bottom, #35546f, #1b3c58);
  box-sizing: border-box;
}

.content-wrapper {
  background: #2b4964;
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  width: 100%;
}

.downloads-title {
  font-size: 24px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 20px;
}

.downloads-description {
  font-size: 16px;
  color: #d0d0d0;
  text-align: center;
  margin-bottom: 30px;
  line-height: 1.6;
}

.beta-login-text {
  font-size: 14px;
  color: #d0d0d0;
  margin-bottom: 10px;
  text-align: center;
}

.signin-button {
  display: flex;
  align-items: center;
  background: radial-gradient(#274660, #375872);
  padding: 12px 30px;
  border-radius: 30px;
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: transform 0.2s;
  text-decoration: none;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.signin-button:hover {
  transform: scale(0.98);
}

.signin-button:active {
  transform: scale(0.95);
}

.ms-icon {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}
