body {
  background: linear-gradient(to bottom, #35546f, #1b3c58);
  background-attachment: fixed;
  margin: 0;
  font-family: Arial, sans-serif;
}

.About {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 180px;
  box-sizing: border-box;
}

.content-wrapper {
  background: #2b4964;
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  width: 100%;
}

h1, h2 {
  color: white;
  margin-bottom: 20px;
}

h3 {
  color: #a2c7e8;
  margin-bottom: 20px;
}

p, ul {
  color: white;
  text-align: left;
  max-width: 800px;
}

ul {
  padding-left: 20px;
}

li {
  margin-bottom: 10px;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #1b3c58;
}

::-webkit-scrollbar-thumb {
  background: #35546f;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: #507fa9;
}

html {
  scrollbar-color: #35546f #1b3c58;
  scrollbar-width: thin;
}
